<template>
    <div v-if="showModal" class="modal-shadow" @click.self="showExitModal=true">
        <div v-if="showExitModal" class="exitModal">
            <p> Вы уверены что хотите прервать тестирование? Результаты не будут сохранены</p>
            <div class="button-container">
                <button class="btn-next" @click="closeModal">Да</button>
                <button class="btn-save" @click="showExitModal=false">Нет</button>
            </div>
        </div>
        <div class="modal wrapper ">
            <div id="quiz" class="quiz ">
                <div v-if="!showResult" id="indicator" class="quiz-indicator">
                    <span class="step">Шаг {{currentQuestionIndex+1}} из {{questions.length}}</span>
                    <div class="empty-indicator">
                        <div class="full-indicator"
                             :style="{width: ((currentQuestionIndex+1)/questions.length)*100+ '%'}"></div>
                    </div>
                </div>
                <div v-if="!showResult" class="slider-container">
                    <div id="questions" class="quiz-questions  slider-track">
                        <div v-for="(question,i) in questions" :key="i" class="quiz-questions-item">
                            <div class="quiz-questions-item__question">{{question.question}}</div>
                            <div class="quiz-questions-item__answers">
                                <ul>
                                    <li v-for="answer of question.answers" :key="answer.id">
                                        <label>
                                            <input v-model="results[currentQuestionIndex]"
                                                   :type="question.type"
                                                   :class="{errorInput:error}"
                                                   :value="answer.id"
                                                   placeholder="Введите ответ"
                                                   class="answer-input"/>
                                            {{question.type==="radio"?answer.value:null}}
                                        </label>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-else id="results" class="quiz-results">
                    <strong class="result">{{results[0]}}, вы прошли тест на: <span
                            :class="{wrongAnswer:finalResult<40, rightAnswer:finalResult>60}">{{finalResult.toFixed(2)}}%</span></strong>
                    <div v-for="(res,i) in questions" :key="i" class="quiz-results-item">
                        <p class="quiz-results-item__question">{{res.question}}</p>
                        <div class="quiz-results-item__answers">
                            <ul>
                                <li v-for="answer of res.answers" :key="answer.id">
                                    <label v-if="res.type==='radio'" class="answer"
                                           :class="{ wrongAnswer: answer.id === results[res.id] && answer.correct === false,
					rightAnswer: answer.correct === true}">
                                        {{answer.id===results[res.id]?
                                        answer.value +" - Ваш ответ"
                                        : answer.value}}
                                    </label>
                                    <label v-else class="answer"
                                           :class="{ wrongAnswer: answer.value.toUpperCase() !== results[res.id].toUpperCase(),
					rightAnswer: answer.value.toUpperCase() === results[res.id].toUpperCase() }">
                                        Ваш ответ: {{results[res.id]}}
                                    </label>

                                </li>

                            </ul>
                        </div>
                    </div>
                </div>

                <div class="quiz-controls">
                    <div v-if="error" class="error">
                        Вы не ответили на вопрос
                    </div>
                    <button v-if="!showResult" id="btn-next" class="btn-next" @click="formClick">
                        Далее
                    </button>
                    <button v-else id="btn-restart" class="btn-save" @click="saveUserResult">Сохранить</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

	import Questions from "../../questions.json"

	export default {
		components: {},

		props: {
			addUserFormShow: {
				type: Boolean,
			}
		},
		data() {
			return {
				wrongAnswer: false,
				showResult: false,
				results: {},
				position: 0,
				questions: Questions,
				currentQuestionIndex: 0,
				showModal: true,
				finalResult: 0,
				error: false,
				showExitModal: false,
			}
		},
		methods: {
			formClick() {
				const isNotLastQuestion = this.questions.length !== this.currentQuestionIndex + 1;
				if (isNotLastQuestion) {
					if (this.results[this.currentQuestionIndex]) {
						this.error = false;
						this.currentQuestionIndex = this.currentQuestionIndex + 1;

						let itemWidth = document.querySelector(".quiz").clientWidth
						let itemsLeft = this.questions.length - (Math.abs(this.position) + itemWidth) / itemWidth;
						this.position -= itemsLeft >= 1 ? itemWidth : itemsLeft * itemWidth;
						document.querySelector(".slider-track").style.transform = `translateX(${this.position}px)`;

					} else {
						this.error = true;
					}
				} else {
					if (this.results[this.currentQuestionIndex]) {
						this.summarizeResult();
						this.questions.shift()
						this.showResult = true;
						this.error = false;

					} else {
						this.error = true;
					}
				}
			},
			summarizeResult() {
				this.questions.forEach((question) => {
					if (question.type === "radio") {
						question.answers.forEach((answer) => {
							if (answer.id === this.results[question.id] && answer.correct === true) {
								this.finalResult = this.finalResult + 1
							}
						})
					} else {
						if (question.id !== 0) {
							if (question.answers[0].value.toUpperCase() === this.results[question.id].toUpperCase()) {
								this.finalResult = this.finalResult + 1
							}
						}
					}
				})

				this.finalResult = this.finalResult / (this.questions.length - 1) * 100;
				this.finalResult.toFixed(2);
			},

			closeModal() {
				this.showModal = false;
				this.results = {};
				window.document.body.classList.remove("modal-active");
				this.$emit("form-close");
			},
			saveUserResult() {
				this.$emit("save-user-result", this.results[0], this.finalResult.toFixed(2));
				this.questions.unshift({
					"question": "Введите ваше имя",
					"id": 0,
					"type": "text",
					"answers": [
						{
							"id": "1"
						}
					]
				});
				this.closeModal();
			}

		}
	}


</script>

